import s from './Discount.module.css';
import {Link} from 'react-router-dom';
import {Privacy} from '../../Pivacy/Privacy';
import {useForm} from 'react-hook-form';
import React from 'react';
import axios from 'axios';
import InputElement from 'react-input-mask';

export const Discount = () => {
    const normalizePhone = phone => {
        return parseInt(phone.replace(/[^0-9]/g, ''));
    };

    const handleData = e => {
        axios({
            method: 'post',
            url: 'http://127.0.0.1:8000/appeals/create/individual',
            headers: {},
            data: {
                username: e.username,
                telephone: e.telephone,
                email: e.email,
                comment: e.comment,
            },
        }).then(response => {
            // console.log(response);
        });
        reset({telephone: '', username: '', email: '', comment: ''});
    };

    const {
        register,
        formState: {errors, isValid},
        handleSubmit,
        reset,
    } = useForm({mode: 'onBlur'});

    return (
        <div className={s.container}>
            <h2>
                <span>
                     50<span>%</span>
                </span>{' '}
                    
            </h2>
            <form
                autoComplete="off"
                className={s.form_order}
                onSubmit={handleSubmit(handleData)}>
                <div className={s.order_input_high}>
                    <div className={s.input_wrapp}>
                        <label className="label">  </label>
                        <input
                            className={s.input_field}
                            {...register('username', {
                                required: '   ',
                                minLength: {
                                    value: 3,
                                    message: '  3 ',
                                },
                            })}
                        />
                        <div className="error">{errors?.username?.message}</div>
                    </div>
                    <div className={s.input_wrapp}>
                        <label className="label"> </label>
                        <input
                            className={s.input_field}
                            {...register('email', {
                                required: '   ',
                                pattern: {
                                    value: /^((([0-9A-Za-z]{1}[-0-9A-z\.]{1,}[0-9A-Za-z]{1})|([0-9--]{1}[-0-9-\.]{1,}[0-9--]{1}))@([-A-Za-z]{1,}\.){1,2}[-A-Za-z]{2,})$/u,
                                    message: '  email',
                                },
                            })}
                        />
                        <div className="error">{errors?.email?.message}</div>
                    </div>
                </div>
                <div className={s.order_input_low}>
                    <div>
                        <label className="label"></label>
                        <InputElement
                            className={s.input_field}
                            mask="+7 (999) 999-99-99"
                            maskChar=" "
                            normalize={normalizePhone}
                            {...register('telephone', {
                                required: '   ',
                                minLength: {
                                    value: 11,
                                    message: '  11 ',
                                },
                            })}
                        />
                        <div className="error">
                            {errors?.telephone?.message}
                        </div>
                    </div>
                    <div>
                        <label className="label"></label>
                        <textarea
                            type="text"
                            className={s.input_field}
                            {...register('comment', {
                                required: '   ',
                            })}
                        />
                        <div className="error">{errors?.comment?.message}</div>
                    </div>
                    <div className={s.order_checkbox}>
                        <input type="checkbox" />
                        <label className={s.label_order}>
                             {' '}
                            <Link to="/privacy" component={<Privacy />}>
                                <span className={s.label_order_span}>
                                     
                                </span>
                            </Link>
                        </label>
                    </div>
                    <button className={s.btn_order} type="submit">
                        
                    </button>
                </div>
            </form>
        </div>
    );
};
