
import React, { useState, useEffect, useCallback } from "react";
import s from "./categories.module.css";
import { MAFbutton } from "../../ModuleComponent/MAFbutton/MAFbutton";
import { CatalogLine } from "../../ModuleComponent/CatalogLine/CatalogLine";
import axios from "axios";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { addToBasket } from "../../redux/BasketSlice";
import { addCustomerData } from "../../redux/OrdersSlice";
import { useRef } from "react";
import { Project } from "../../ProjectPage/Project";
import { Info } from "../Product_Info/Info";

export const Categories = () => {
   const navigate = useNavigate();
   const location = useLocation();
   const [active, setActive] = useState("Grid");
   const [animation, setAnimation] = useState(false);
   const [catList, setCatList] = useState(null);
   const [catParams, setCatParams] = useState(
      location.state ? [location.state.id] : []
   );
   const [matList, setMatList] = useState(null);
   const [matParams, setMatParams] = useState([]);
   const [products, setProducts] = useState([]);
   const [ordering, setOrdering] = useState("name");
   const [page, setPage] = useState(1);
   const [loading, setLoading] = useState(false);
   const [nextPage, setNextPage] = useState("");
   const [valueSearch, setValueSearch] = useState("");
   const [name, setName] = useState();
   const [tel, setTel] = useState();
   const [email, setEmail] = useState();
   const [sortParams, setSortParams] = useState(0);
   console.log(sortParams);
   const dispatch = useDispatch();
   const dataBasket = useSelector((state) => state.basket.dataBasket);
   const openAside = useRef(null);

   useEffect(() => {
      document.addEventListener("scroll", scrollHandler);
      return function () {
         document.removeEventListener("scroll", scrollHandler);
      };
   }, [nextPage, loading]);
   useEffect(() => {
      setPage(1);
   }, [ordering]);
   const scrollHandler = (e) => {
      if (
         e.target.documentElement.scrollHeight -
            (e.target.documentElement.scrollTop + window.innerHeight) <
            50 &&
         nextPage &&
         !loading
      ) {
         setPage(page + 1);
      }
   };
   useEffect(() => {
      axios.get("http://127.0.0.1:8000/categories/").then((response) => {
         setCatList(response.data);
      });
      axios.get("http://127.0.0.1:8000/materials/").then((response) => {
         setMatList(response.data);
      });
   }, []);
   useEffect(() => {
      setLoading(true);
      axios
         .get(`http://127.0.0.1:8000/products/${location.search}`)
         .then((response) => {
            setProducts(products.concat(response.data.results));
            setNextPage(response.data.next);
            setLoading(false);
         });
   }, [location.search]);

   const SetOptions = useCallback(
      (id, type) => {
         if (id !== undefined) {
            if (type === "cat") {
               if (catParams.indexOf(id) == -1) {
                  setCatParams([...catParams, id]);
               } else {
                  let index = catParams.indexOf(id);
                  let arr = catParams;
                  arr.splice(index, 1);
                  setCatParams([...arr]);
               }
            } else if (type === "mat") {
               if (matParams.indexOf(id) == -1) {
                  setMatParams([...matParams, id]);
               } else {
                  let index = matParams.indexOf(id);
                  let arr = matParams;
                  arr.splice(index, 1);
                  setMatParams([...arr]);
               }

            }
            setProducts([]);
            setPage(1);
        }},
        [catParams, matParams]
    );

    useEffect(() => {
        setProducts([]);
    }, [ordering]);


   useEffect(() => {
      valueSearch && setProducts([]);
      navigate(
         `/categories/?page=${page}${
            catParams.length ? `&category=${catParams.join("&category=")}` : ""
         }${
            matParams.length ? `&material=${matParams.join("&material=")}` : ""
         }${`&search=${valueSearch}`}&ordering=${ordering}`
      );
   }, [catParams, matParams, ordering, page, valueSearch]);


    return (
        <>
        <div className={s.catalog}>
            <div
                className={`${
                    animation === true ? 'categories_active' : ''
                }`}></div>
            <MAFbutton />
            <div className={s.setting}>
                <div className={s.div_li}>
                    <ul className={s.list_setting}>
                        <li>
                            <Link to="/"></Link>
                        </li>
                        <li className="Component-arrow-0-2-11"></li>
                        <li>
                            <Link to="/Catalog"></Link>
                        </li>
                        <li className="Component-arrow-0-2-11"></li>
                        <li className={s.li_color}>
                               
                        </li>
                    </ul>
                </div>
                <CatalogLine
                    catList={catList}
                    valueSearch={valueSearch}
                    setValueSearch={setValueSearch}
                    //sortParams={sortParams}
                    setSortParams={setSortParams}
                />
                <aside ref={openAside} className={s.list_category}>
                    <div className={s.aside_setting}>
                        <div
                            className={s.list}
                            onClick={e => SetOptions(e.target.value, 'cat')}>
                            <p></p>
                            {catList &&
                                catList.map(value => {
                                    return (
                                        <div key={value.id}>
                                            <label>
                                                <input
                                                    value={value.id}
                                                    type="checkbox"
                                                />
                                                {value.name}
                                            </label>
                                        </div>
                                    );
                                })}
                        </div>
                        <div
                            className={s.list}
                            onClick={e => SetOptions(e.target.value, 'mat')}>
                            <p></p>
                            {matList &&
                                matList.map(value => {
                                    return (
                                        <div key={value.id}>
                                            <label>
                                                <input
                                                    value={value.id}
                                                    type="checkbox"
                                                />
                                                {value.name}
                                            </label>
                                        </div>
                                    );
                                })}
                        </div>
                    </div>
                </aside>
                <section className={s.section_setting}>
                    <div className={s.panel}>
                        <div className={s.grid}>
                            <button
                                onClick={() => setActive('Grid')}
                                className={` active_button_grid ${
                                    active === 'Grid'
                                        ? 'active_button_grid'
                                        : 'no_active_rows'
                                }`}>
                                Grid
                            </button>
                            <button
                                onClick={() => setActive('Row')}
                                className={`${
                                    active === 'Row'
                                        ? 'active_button_rows'
                                        : 'no_active_grid'
                                } `}>
                                Rows
                            </button>
                        </div>
                        <div className={s.categories}>
                            <div> </div>
                            <select
                                onChange={e => {
                                    setOrdering(e.target.value);
                                }}>
                                <option value={'name'}>
                                     ( .)
                                </option>
                                <option value={'-name'}>
                                     ( .)
                                </option>
                                <option value={'price'}> ( .)</option>
                                <option value={'-price'}>
                                     ( .)
                                </option>
                            </select>
                        </div>
                    </div>
                    {loading ? (
                        <div className="preloader">
                            <svg
                                className="preloader__image"
                                role="img"
                                xmlns="http://www.w3.org/2000/svg"
                                viewBox="0 0 512 512">
                                <path
                                    fill="currentColor"
                                    d="M304 48c0 26.51-21.49 48-48 48s-48-21.49-48-48 21.49-48 48-48 48 21.49 48 48zm-48 368c-26.51 0-48 21.49-48 48s21.49 48 48 48 48-21.49 48-48-21.49-48-48-48zm208-208c-26.51 0-48 21.49-48 48s21.49 48 48 48 48-21.49 48-48-21.49-48-48-48zM96 256c0-26.51-21.49-48-48-48S0 229.49 0 256s21.49 48 48 48 48-21.49 48-48zm12.922 99.078c-26.51 0-48 21.49-48 48s21.49 48 48 48 48-21.49 48-48c0-26.509-21.491-48-48-48zm294.156 0c-26.51 0-48 21.49-48 48s21.49 48 48 48 48-21.49 48-48c0-26.509-21.49-48-48-48zM108.922 60.922c-26.51 0-48 21.49-48 48s21.49 48 48 48 48-21.49 48-48-21.491-48-48-48z"></path>
                            </svg>
                        </div>
                    ) : (
                        // <div className={s.product}>
                        <div
                            className={
                                active === 'Grid'
                                    ? `${s.product}`
                                    : `${s.product_line}`
                            }>
                            {products.length
                                ? products
                                      .filter(obj => {
                                          if (
                                              obj.category.type === sortParams
                                          ) {
                                              return true;
                                          } else if (sortParams === 0) {
                                              return obj;
                                          }
                                          return false;
                                      })
                                      .map(value => {
                                          console.log('products:', value);

                                          return (
                                              <div
                                                  key={value.id}
                                                  className={
                                                      active === 'Grid'
                                                          ? `${s.buy}`
                                                          : `${s.buy_line}`
                                                  }>
                                                  <Link
                                                      to={`/info/${value.id}`}
                                                      state={{
                                                          from: `${value.id}`,
                                                      }}
                                                      className={
                                                          active === 'Grid'
                                                              ? `${s.a_setting}`
                                                              : `${s.a_setting_line}`
                                                      }>
                                                      <img src={value.image} />
                                                  </Link>
                                                  <div
                                                      className={
                                                          active === 'Grid'
                                                              ? `${s.product_setting}`
                                                              : `${s.product_setting_line}`
                                                      }>
                                                      <a>
                                                          <div
                                                              className={
                                                                  s.text
                                                              }>
                                                              {value.name}
                                                          </div>
                                                          <div
                                                              className={
                                                                  active ===
                                                                  'Grid'
                                                                      ? `${s.product_descrip_none}`
                                                                      : `${s.product_descrip_block}`
                                                              }>
                                                              <p
                                                                  className={
                                                                      s.descrip_line_text
                                                                  }>
                                                                  {
                                                                      value.description
                                                                  }
                                                              </p>
                                                          </div>
                                                      </a>
                                                      {/*<div className={s.margin}>&nbsp;</div>*/}
                                                      <div
                                                          className={
                                                              active === 'Grid'
                                                                  ? `${s.button_setting}`
                                                                  : `${s.button_setting_line}`
                                                          }>
                                                          <button
                                                              onClick={() => {
                                                                  setAnimation(
                                                                      !animation,
                                                                  );
                                                                  dispatch(
                                                                      addToBasket(
                                                                          value,
                                                                      ),
                                                                  );
                                                              }}>
                                                              <a></a>
                                                          </button>
                                                      </div>
                                                  </div>
                                              </div>
                                          );
                                      })
                                : ' '}
                        </div>
                    )}
                </section>
            </div>

            <CatalogLine
               catList={catList}
               valueSearch={valueSearch}
               setValueSearch={setValueSearch}
               //sortParams={sortParams}
               setSortParams={setSortParams}
            />
            <aside ref={openAside} className={s.list_category}>
               <div className={s.aside_setting}>
                  <div
                     className={s.list}
                     onClick={(e) => SetOptions(e.target.value, "cat")}
                  >
                     <p></p>
                     {catList &&
                        catList.map((value) => {
                           return (
                              <div key={value.id}>
                                 <label>
                                    <input value={value.id} type="checkbox" />
                                    {value.name}
                                 </label>
                              </div>
                           );
                        })}
                  </div>
                  <div
                     className={s.list}
                     onClick={(e) => SetOptions(e.target.value, "mat")}
                  >
                     <p></p>
                     {matList &&
                        matList.map((value) => {
                           return (
                              <div key={value.id}>
                                 <label>
                                    <input value={value.id} type="checkbox" />
                                    {value.name}
                                 </label>
                              </div>
                           );
                        })}
                  </div>
               </div>
            </aside>
            <section className={s.section_setting}>
               <div className={s.panel}>
                  <div className={s.grid}>
                     <button
                        onClick={() => setActive("Grid")}
                        className={` active_button_grid ${
                           active === "Grid"
                              ? "active_button_grid"
                              : "no_active_rows"
                        }`}
                     >
                        Grid
                     </button>
                     <button
                        onClick={() => setActive("Row")}
                        className={`${
                           active === "Row"
                              ? "active_button_rows"
                              : "no_active_grid"
                        } `}
                     >
                        Rows
                     </button>
                  </div>
                  <div className={s.categories}>
                     <div> </div>
                     <select
                        onChange={(e) => {
                           setOrdering(e.target.value);
                        }}
                     >
                        <option value={"name"}> ( .)</option>
                        <option value={"-name"}> ( .)</option>
                        <option value={"price"}> ( .)</option>
                        <option value={"-price"}> ( .)</option>
                     </select>
                  </div>
               </div>
               {loading ? (
                  <div className="preloader">
                     <svg
                        className="preloader__image"
                        role="img"
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 512 512"
                     >
                        <path
                           fill="currentColor"
                           d="M304 48c0 26.51-21.49 48-48 48s-48-21.49-48-48 21.49-48 48-48 48 21.49 48 48zm-48 368c-26.51 0-48 21.49-48 48s21.49 48 48 48 48-21.49 48-48-21.49-48-48-48zm208-208c-26.51 0-48 21.49-48 48s21.49 48 48 48 48-21.49 48-48-21.49-48-48-48zM96 256c0-26.51-21.49-48-48-48S0 229.49 0 256s21.49 48 48 48 48-21.49 48-48zm12.922 99.078c-26.51 0-48 21.49-48 48s21.49 48 48 48 48-21.49 48-48c0-26.509-21.491-48-48-48zm294.156 0c-26.51 0-48 21.49-48 48s21.49 48 48 48 48-21.49 48-48c0-26.509-21.49-48-48-48zM108.922 60.922c-26.51 0-48 21.49-48 48s21.49 48 48 48 48-21.49 48-48-21.491-48-48-48z"
                        ></path>
                     </svg>
                  </div>
               ) : (
                  // <div className={s.product}>
                  <div
                     className={
                        active === "Grid" ? `${s.product}` : `${s.product_line}`
                     }
                  >
                     {products.length
                        ? products
                             .filter((obj) => {
                                if (sortParams === 0) {
                                   return obj
                                } else if (obj.category.type === sortParams) {
											return true;
										}
                                return false;
                             })
                             .map((value) => {
                                console.log("products:", value);

                                return (
                                   <div
                                      key={value.id}
                                      className={
                                         active === "Grid"
                                            ? `${s.buy}`
                                            : `${s.buy_line}`
                                      }
                                   >
                                      <Link
                                         to={`/info/${value.id}`}
                                         state={{ from: `${value.id}` }}
                                         className={
                                            active === "Grid"
                                               ? `${s.a_setting}`
                                               : `${s.a_setting_line}`
                                         }
                                      >
                                         <img src={value.image} />
                                      </Link>
                                      <div
                                         className={
                                            active === "Grid"
                                               ? `${s.product_setting}`
                                               : `${s.product_setting_line}`
                                         }
                                      >
                                         <a>
                                            <div className={s.text}>
                                               {value.name}
                                            </div>
                                            <div
                                               className={
                                                  active === "Grid"
                                                     ? `${s.product_descrip_none}`
                                                     : `${s.product_descrip_block}`
                                               }
                                            >
                                               <p
                                                  className={
                                                     s.descrip_line_text
                                                  }
                                               >
                                                  {value.description}
                                               </p>
                                            </div>
                                         </a>
                                         {/*<div className={s.margin}>&nbsp;</div>*/}
                                         <div
                                            className={
                                               active === "Grid"
                                                  ? `${s.button_setting}`
                                                  : `${s.button_setting_line}`
                                            }
                                         >
                                            <button
                                               onClick={() => {
                                                  setAnimation(!animation);
                                                  dispatch(addToBasket(value));
                                               }}
                                            >
                                               <a></a>
                                            </button>
                                         </div>
                                      </div>
                                   </div>
                                );
                             })
                        : " "}
                  </div>
               )}
            </section>
         </div>

         <aside className={`${animation ? s.basket_animation : s.basket} `}>
            <img
               onClick={() => setAnimation(!animation)}
               src="data:image/png;base64,"
               alt="close"
               className={s.basket_close}
            />
            <h2 className={s.basket_h2}></h2>
            {dataBasket.map((item) => {
               return (
                  <div className={s.preview}>
                     <a href='/products/LGO2-Avtobusnaya ostanovka "Siti"'>
                        <div>
                           <img
                              src={item.image}
                              alt="bench"
                              className={s.preview_img}
                           />
                           <p className="Component-itemInfo-0-2-40">
                              {item.name}
                           </p>
                        </div>
                     </a>
                  </div>
               );
            })}
            <Link to="/Basket">
               <button className={s.basket_button}> </button>
            </Link>
            <div className={s.basket_form}>
               {/* <OrderForm /> */}
               <label>
                  <p></p>
                  <input
                     type="text"
                     name="firstname"
                     required=""
                     value={name}
                     onChange={(e) => setName(e.target.value)}
                  />
               </label>
               <label>
                  <p></p>
                  <input
                     type="text"
                     name="phone"
                     required=""
                     value={tel}
                     onChange={(e) => setTel(e.target.value)}
                  />
               </label>
               <label>
                  <p> </p>
                  <input
                     type="email"
                     name="email"
                     required=""
                     value={email}
                     onChange={(e) => setEmail(e.target.value)}
                  />
               </label>
               <Link to="/Basket">
                  <button
                     className={s.basket_confirm}
                     onClick={(e) =>
                        dispatch(addCustomerData({ name, tel, email }))
                     }
                  >
                     
                  </button>
               </Link>
            </div>
         </aside>

</>
   );

};
